/* eslint-disable @typescript-eslint/no-explicit-any */
'use client';
import React, { useState, useEffect } from 'react';
import {
  Box,
  Typography,
  Grid,
  Card,
  CardContent,
  Divider,
  Select,
  MenuItem,
  FormControl,
  InputLabel,
  Button,
  Tooltip,
  CircularProgress,
} from '@mui/material';
import { LineChart } from '@mui/x-charts/LineChart';
import { BarChart } from '@mui/x-charts/BarChart';
import { Gauge } from '@mui/x-charts/Gauge';
import RefreshIcon from '@mui/icons-material/Refresh';

const RealTimeInsights: React.FC = () => {
  const [timeRange, setTimeRange] = useState('monthly');
  const [sales, setSales] = useState<any[]>([]);
  const [revenue, setRevenue] = useState<any[]>([]);
  const [lastUpdated, setLastUpdated] = useState(new Date());
  const [loading, setLoading] = useState(false);

  // Simulate API data fetching
  const fetchData = async () => {
    try {
      setLoading(true);
      // Simulated data fetch logic
      const mockSales = Array.from({ length: 12 }, (_, i) => ({
        x: `2024-01-${i + 1}`,
        y: Math.random() * 1000,
      }));
      const mockRevenue = Array.from({ length: 12 }, (_, i) => ({
        x: `2024-01-${i + 1}`,
        y: Math.random() * 5000,
      }));

      setSales(mockSales);
      setRevenue(mockRevenue);
      setLastUpdated(new Date());
    } catch (error) {
      console.error('Error fetching data:', error);
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    fetchData();
    const interval = setInterval(fetchData, 10000); // Fetch data every 10 seconds
    return () => clearInterval(interval);
  }, []);

  const handleRefresh = () => {
    fetchData();
  };

  return (
    <Box
  sx={{
    p: { xs: 2, md: 4 },
    backgroundColor: 'background.default',
    minHeight: '100vh',
    color: 'text.primary',
  }}
>
  <Typography variant="h4" gutterBottom sx={{ fontWeight: 600, color: 'primary.main' }}>
    Real-Time Insights
  </Typography>

  <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 4 }}>
    <FormControl size="small" sx={{ minWidth: 150 }}>
      <br />
      <InputLabel id="time-range-label">Time Range</InputLabel>
      <Select
        labelId="time-range-label"
        value={timeRange}
        onChange={(e) => setTimeRange(e.target.value)}
        sx={{ backgroundColor: 'background.paper' }}
      >
        <MenuItem value="daily">Daily</MenuItem>
        <MenuItem value="weekly">Weekly</MenuItem>
        <MenuItem value="monthly">Monthly</MenuItem>
        <MenuItem value="yearly">Yearly</MenuItem>
      </Select>
    </FormControl>
    <Tooltip title="Refresh Data">
      <Button
        variant="outlined"
        startIcon={<RefreshIcon />}
        onClick={handleRefresh}
        disabled={loading}
        sx={{ color: 'secondary.main', borderColor: 'secondary.main' }}
      >
        {loading ? <CircularProgress size={24} /> : 'Refresh'}
      </Button>
    </Tooltip>
    <Typography variant="body2" color="text.secondary">
      Last updated: {lastUpdated.toLocaleTimeString()}
    </Typography>
  </Box>

  <Grid container spacing={4}>
    <Grid item xs={12} md={6}>
      <Card
        sx={{
          borderRadius: 3,
          boxShadow: 3,
          transition: 'transform 0.3s',
          '&:hover': { transform: 'scale(1.02)' },
          color: 'text.primary',
        }}
      >
        <CardContent>
          <Typography variant="h6" gutterBottom>
            Monthly Sales
          </Typography>
          <Divider sx={{ mb: 2, borderColor: 'primary.main' }} />
          <LineChart
            xAxis={[{ data: sales.map((d) => d.x) }]}
            series={[
              {
                data: sales.map((d) => d.y),
                color: '#4caf50', // Line color: green
              },
            ]}
            width={400}
            height={300}
          />
        </CardContent>
      </Card>
    </Grid>

    <Grid item xs={12} md={6}>
      <Card
        sx={{
          borderRadius: 3,
          boxShadow: 3,
          transition: 'transform 0.3s',
          '&:hover': { transform: 'scale(1.02)' },
          color: 'text.primary',
        }}
      >
        <CardContent>
          <Typography variant="h6" gutterBottom>
            Monthly Revenue
          </Typography>
          <Divider sx={{ mb: 2, borderColor: 'primary.main' }} />
          <BarChart
            xAxis={[{ data: revenue.map((d) => d.x), scaleType: 'band' }]}
            series={[{ data: revenue.map((d) => d.y), color: '#ff5722' }]} 
            width={400}
            height={300}
          />
        </CardContent>
      </Card>
    </Grid>

    <Grid item xs={12}>
      <Card
        sx={{
          borderRadius: 3,
          boxShadow: 3,
          p: 3,
          display: 'flex',
          justifyContent: 'space-evenly',
          flexWrap: 'wrap',
          backgroundColor: 'primary.light',
          color: 'text.primary',
        }}
      >
        {[{ label: 'Sales Target', value: 75 }, { label: 'Revenue Growth', value: 50 }, { label: 'Active Users', value: 65 }].map(
          (gauge, index) => (
            <Box key={index} sx={{ textAlign: 'center', m: 2 }}>
              <Typography variant="h6" gutterBottom>
                {gauge.label}
              </Typography>
              <Gauge
                width={120}
                height={120}
                value={gauge.value}
                valueMin={0}
                valueMax={100}
                sx={{ color: 'primary.main' }}
              />
            </Box>
          )
        )}
      </Card>
    </Grid>
  </Grid>
</Box>

  );
};

export default RealTimeInsights;
